
<template>
  <div class="home create">
    <!-- 创建 -->
    <el-card class="box-card create-box-card">
      <div class="text item" @click="go_create">
        <div class="icon-box">
          <span class="iconfont icon-xinjian"></span>
        </div>
        <p>创建</p>
      </div>
    </el-card>

    <!-- 停止收集 -->
    <el-card class="box-card created-box-card" v-for="item in qsList">
      <div class="text item">
        <div class="top">
          <div class="name">{{item.title}}</div>
          <div class="date">{{item.time|filterTime}}</div>
        </div>
        <div class="bottom show-bottom">
          <div class="left">0 份数据</div>
          <div class="right cole6a">{{item.stateTitle}}</div>
        </div>
        <div class="bottom hover-bottom">
          <div class="left">
            <el-tooltip content="编辑" effect="light">
              <el-button @click="edit_report(item.state ,item.num)">
                <span class="iconfont icon-bianji1"></span>
              </el-button>
            </el-tooltip>
            <el-tooltip content="分享" effect="light">
              <el-button @click="release(item.state)">
                <span class="iconfont icon-fenxiang"></span>
              </el-button>
            </el-tooltip>
            <el-tooltip content="预览" effect="light">
              <el-button @click="go_preview(item.num)">
                <span class="iconfont icon-chakan"></span>
              </el-button>
            </el-tooltip>
          </div>
          <div class="right">
            <el-row class="block-col-2">
              <el-col :span="12">
                <el-dropdown trigger="click" placement="bottom-end">
                  <span class="el-dropdown-link">
                    <span class="iconfont icon-liebiao"></span>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item>导出</el-dropdown-item>
                    <el-dropdown-item>复制</el-dropdown-item>
                    <el-dropdown-item>
                      <div @click="ready_delete(item.num)">删除</div>
                    </el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              </el-col>
            </el-row>
          </div>
        </div>
      </div>
    </el-card>
    <!-- 收集中 -->
    <!-- <el-card class="box-card created-box-card">
      <div class="text item">
        <div class="top">
          <div class="name">问卷标题问卷标题问卷标题问</div>
          <div class="date">17分钟前</div>
        </div>
        <div class="bottom show-bottom">
          <div class="left">0 份数据</div>
          <div class="right col67c">收集中</div>
        </div>
        <div class="bottom hover-bottom">
          <div class="left">
            <el-tooltip content="回收" effect="light">
              <el-button>
                <span class="iconfont icon-ai49"></span>
              </el-button>
            </el-tooltip>
            <el-tooltip content="查看" effect="light">
              <el-button>
                <span class="iconfont icon-chakan"></span>
              </el-button>
            </el-tooltip>
          </div>
          <div class="right">
            <el-row class="block-col-2">
              <el-col :span="12">
                <el-dropdown trigger="click" placement="bottom-end">
                  <span class="el-dropdown-link">
                    <span class="iconfont icon-liebiao"></span>
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item>导出</el-dropdown-item>
                    <el-dropdown-item>复制</el-dropdown-item>
                    <el-dropdown-item>删除</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              </el-col>
            </el-row>
          </div>
        </div>
      </div>
    </el-card>-->
    <!-- 删除问卷提示框 -->
    <div class="shadow" v-if="showDialog">
      <div class="del-dialog">
        <header>
          <span>提示</span>
          <span class="close-btn" @click="showDialog = false">X</span>
        </header>
        <p>{{info}}</p>
        <div class="btn-box">
          <button class="yes" @click="delItem">确定</button>
          <button @click="showDialog = false">取消</button>
        </div>
      </div>
    </div>
  </div>
  <!-- 提示框 -->
</template>

<script>
import storage from "@/vuex/store.js";
export default {
  name: "create",
  data() {
    return {
      qsList: [],
      showDialog: false,
      iterator: {},
      info: "",
      showDialog: false,
      del_item: undefined
    };
  },
  mounted() {
    if (storage.get() !== null) {
      this.qsList = storage.get();
    } else {
      storage.save([
        {
          num: 1,
          title: "第一份问卷1",
          time: "1545985962692",
          state: "inissue",
          stateTitle: "收集中",
          checked: false,
          question: [
            {
              num: "Q1",
              title: "单选题",
              type: "radio",
              isNeed: true,
              options: ["选项一", "选项二"]
            },
            {
              num: "Q2",
              title: "多选题",
              type: "checkbox",
              isNeed: true,
              options: ["选项一", "选项二", "选项三", "选项四"]
            },
            { num: "Q3", title: "文本题", type: "textarea", isNeed: true }
          ]
        },

        {
          num: 2,
          title: "第二份问卷",
          time: "1545985962692",
          state: "noissue",
          stateTitle: "已收集",
          checked: false,
          question: [
            {
              num: "Q1",
              title: "单选题",
              type: "radio",
              isNeed: true,
              options: ["选项一", "选项二"]
            },
            {
              num: "Q2",
              title: "多选题",
              type: "checkbox",
              isNeed: true,
              options: ["选项一", "选项二", "选项三", "选项四"]
            },
            { num: "Q3", title: "文本题", type: "textarea", isNeed: true }
          ]
        },

        {
          num: 3,
          title: "第三份问卷",
          time: "1545985962692",
          state: "issueed",
          stateTitle: "已收集",
          checked: false,
          question: [
            {
              num: "Q1",
              title: "单选题",
              type: "radio",
              isNeed: true,
              options: ["选项一", "选项二"]
            },
            {
              num: "Q2", //序号
              title: "多选题", //题目
              type: "checkbox",
              isNeed: true,
              options: ["选项一", "选项二", "选项三", "选项四"]
            },
            { num: "Q3", title: "文本题", type: "textarea", isNeed: true }
          ]
        }
      ]);
      this.qsList = storage.get();
    }
  },
  methods: {
    // 创建页面
    go_create() {
      this.$router.push({ path: "/create" });
      
    },
    // 预览页面
    go_preview(num) {
      this.$router.push({
        path: "/preview",
        query: { num: num }
      });
    },
    // 编辑问卷
    edit_report(state,num){
      if(state!=="inissue"){
        return alert("只有未发布的问卷才能编辑")
      }
      this.$router.push({ 
        path:"/edit-create",
        query: { num: num }
       });
    },
    // 发布问卷
    release(state){
       if(state!=="inissue"){
        return alert("问卷已发布，不能重复发布")
      }
    },
    showDialogMsg(info, num) {
      this.showDialog = true;
      this.info = info;
      this.del_item = num;
    },
    ready_delete(num) {
      this.showDialogMsg("确认要删除此问卷", num);
    },
    // 删除问卷
    delItem() {
      let index = 0;
      for (let length = this.qsList.length; index < length; index++) {
        if (this.qsList[index].num === this.del_item) break;
      }
      this.qsList.splice(index, 1);
      this.showDialog = false;
    }
  },
  watch: {
    qsList: {
      handler(val) {
        val.forEach((item, index) => {
          item.num = index + 1;
        });
        storage.save(val);
      },
      // 深度监听
      deep: true
    }
  }
};
</script>
<style scoped lang="scss">
@import "./index.scss";
</style>
<style>
.home .el-card__body {
  width: 100%;
  height: 100%;
}
.home .el-button {
  padding: 0;
  border: 0;
}
</style>


